import React from 'react';
import './OwnedReadBooks.css'
import {Link} from 'react-router-dom'

function OwnedReadBookShelf(props){
    // "Ojqi8KbWuLwC"
    return(
        <>
            <div className="owned-read-container" >
                {props.ownedReadBooks.map((book, idx) =>
                 <Link className="owned-read-book" to={{
                        pathname:`/book-page/${book._id}`,
                    }}>{book.title}</Link>
                )}
            </div>
        </>
    )
}


export default OwnedReadBookShelf